import React, { useState, useEffect, useMemo, useCallback } from 'react'

import { CopyOutlined } from '@ant-design/icons'
import {
  ConfirmModal,
  CoreButton,
  Helmet,
  Input,
  Tooltip,
  Notification,
  AffiliateInput,
} from 'components'
import copy from 'copy-to-clipboard'
import { encode } from 'jwt-simple'
import { getWalletAddressByChain, Percent } from 'multichain-sdk'

import { ADDRESS } from 'multichain-sdk/config'

import { A } from 'components/Link/Link.style'

import { useBalance } from 'hooks/useBalance'

import { multichain } from 'services/multichain'

import { getAffiliates } from 'helpers/storage'

import * as Styled from './Affiliate.style'

const Affiliate = () => {
  const [recipient, setRecipient] = useState('')
  const [chainRecipient, setChainRecipient] = useState('')
  const [link, setLink] = useState('')
  const { wallet } = useBalance()
  const [visibleConfirmModal, setVisibleConfirmModal] = useState(false)
  const handleCopyAddress = useCallback(() => {
    copy(link)

    Notification({
      type: 'info',
      message: 'Address Copied',
      duration: 3,
      placement: 'bottomRight',
    })
  }, [link])
  const getAffiliateLink = useMemo(() => {
    return (
      <Styled.ConfirmModalContent>
        <Styled.CardTitle>Become An Affiliate</Styled.CardTitle>
        <Input disabled value={link} />
        <Tooltip placement="top" tooltip="Copy">
          <CoreButton onClick={handleCopyAddress}>
            <CopyOutlined style={{ color: '#08c' }} />
          </CoreButton>
        </Tooltip>
      </Styled.ConfirmModalContent>
    )
  }, [handleCopyAddress, link])
  useEffect(() => {
    if (wallet) {
      const address = getWalletAddressByChain(wallet, 'THOR')
      setRecipient(address || '')
      setChainRecipient(address || '')
    }
  }, [wallet])

  const affiliatePercentage = useMemo(() => {
    const refs = getAffiliates()
    const affiliateAddresses: string[] = refs ?? []
    const percentage = new Percent(0.8 ** (affiliateAddresses.length + 1))
    return percentage.toFixed(2)
  }, [])

  const handleSend = () => {
    // get affiliates from local storage
    const refs = getAffiliates()
    // decrypt affiliate array
    const affiliateAddresses: string[] = refs ?? []
    affiliateAddresses.push(recipient)
    const filteredAddresses = affiliateAddresses.filter((affiliateAddress) =>
      multichain.validateAddress({
        chain: 'THOR',
        address: affiliateAddress,
      }),
    )
    if (
      multichain.validateAddress({
        chain: 'THOR',
        address: recipient,
      })
    ) {
      // encrypt affilate array
      const encodedQuery = encode(
        JSON.stringify(filteredAddresses),
        ADDRESS as string,
      )
      setLink(`${window.location.origin.toString()}/#/?refs=${encodedQuery}`)
      setVisibleConfirmModal(true)
    } else {
      Notification({
        type: 'error',
        message: 'Invalide Thorchain address',
        description: 'Affiliate links must be valid thorchain addresses',
        duration: 20,
      })
    }
  }
  const handleClose = useCallback(() => {
    setVisibleConfirmModal(false)
  }, [])

  return (
    <Styled.Container>
      <Helmet title="Affiliate" content="Affiliate" />
      <Styled.FromCard>
        <Styled.CardTitle>
          Join the Crosschain.Quest
          <br />
          Affiliate Program
        </Styled.CardTitle>
        <Styled.CardSubTitle>
          {`earn ${affiliatePercentage} of swap fees when you share this with a friend and if your friend becomes an affiliate you earn too!`}
        </Styled.CardSubTitle>
        <A
          href="https://crosschain.quest/docs/Affiliate/Crosschain"
          target="_blank"
          rel="noopener noreferrer"
        >
          Read More
        </A>
        <Styled.CardSubTitle>
          Step 1: Enter your thorchain address
        </Styled.CardSubTitle>
        <AffiliateInput
          title="address"
          address={recipient}
          chain="THOR"
          chainAddr={chainRecipient}
          onAddressChange={setRecipient}
        />
        <Styled.CardSubTitle>
          Step 2: Generate an affilate link
        </Styled.CardSubTitle>
        <Styled.GenerateButton onClick={handleSend} error={false}>
          Generate Link
        </Styled.GenerateButton>
        <Styled.CardSubTitle>Step 3: Share!</Styled.CardSubTitle>
        <Styled.CardSubTitle>
          {!link ? (
            'generate a link to share first!'
          ) : (
            <a
              className="twitter-share-button"
              href={`https://twitter.com/intent/tweet?text=Permissionless%20swaps%20across%20blockchains%20at%20&url=${encodeURIComponent(
                link,
              )}`}
            >
              Tweet
            </a>
          )}
        </Styled.CardSubTitle>
      </Styled.FromCard>
      <ConfirmModal
        visible={visibleConfirmModal}
        onOk={handleClose}
        inputAssets={[]}
        onCancel={handleClose}
      >
        {visibleConfirmModal && getAffiliateLink}
      </ConfirmModal>
    </Styled.Container>
  )
}

export default Affiliate
